<template><section><html><head></head><body><h1>导航罗盘</h1>
<p><code v-pre="">vc-navigation</code> 组件用于加载导航罗盘部件。<strong>注意：</strong> 使用此组件需要引入 <code v-pre="">import 'vue-cesium/lib/vc-navigation.css'</code>，需要<code v-pre="">css-loader</code>。</p>
<h2>示例</h2>
<h3>加载导航罗盘部件</h3>
<h4>预览</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer scene-mode-picker="" :timeline="timeline" @ready="ready">
        <vc-navigation @legendchanged="legendChanged" :options="options"></vc-navigation>
        <vc-layer-imagery>
          <vc-provider-imagery-openstreetmap></vc-provider-imagery-openstreetmap>
        </vc-layer-imagery>
      </vc-viewer>
      <div class="demo-tool">
        <span>罗盘部件</span>
        <md-switch v-model="options.enableCompass"></md-switch>
        <span>缩放部件</span>
        <md-switch v-model="options.enableZoomControl"></md-switch>
        <span>罗盘外环是否可用</span>
        <md-switch v-model="options.enableCompassOuterRing"></md-switch>
        <span>比例尺部件</span>
        <md-switch v-model="options.enableDistanceLegend"></md-switch>
        <span>位置信息部件</span>
        <md-switch v-model="options.enableLocationBar"></md-switch>
      </div>
    </div>
  </template>
  
</doc-preview>
