<h2>属性</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>enableCompass</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> 指定是否启用罗盘部件。</td>
</tr>
<tr>
<td>enableZoomControl</td>
<td>Boolean|Object</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> 指定是否启用缩放部件。</td>
</tr>
<tr>
<td>enableZoomControl.zoomAmount</td>
<td>Number</td>
<td><code v-pre="">2</code></td>
<td><code v-pre="">optional</code> 缩放控件缩放系数。</td>
</tr>
<tr>
<td>enableZoomControl.defaultResetView</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> 重置相机参数。</td>
</tr>
<tr>
<td>enableZoomControl.overrideCamera</td>
<td>Boolean</td>
<td><code v-pre="">false</code></td>
<td><code v-pre="">optional</code> 缩放控件默认相机位置参数是否覆盖 vc-viewer 上的相机参数。</td>
</tr>
<tr>
<td>enableDistanceLegend</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> 指定是否启用距离比例尺部件。</td>
</tr>
<tr>
<td>enableCompassOuterRing</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> 指定罗盘部件是否可用。</td>
</tr>
<tr>
<td>enableLocationBar</td>
<td>Boolean|Object</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> 指定位置信息部件是否可用。</td>
</tr>
<tr>
<td>enablePrintView</td>
<td>Boolean|Object</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> 指定打印部件是否可用。</td>
</tr>
</tbody>
</table>
<hr>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer}</td>
<td>该组件渲染完毕时触发，返回 Cesium 类, viewer 实例。</td>
</tr>
<tr>
<td>legendChanged</td>
<td>number</td>
<td>比例尺改变时触发，返回比例尺数值，单位米。</td>
</tr>
<tr>
<td>geolocation</td>
<td>Object</td>
<td>定位成功时触发，返回定位结果。</td>
</tr>
</tbody>
</table>
</body></html></section></template>

<script>
    export default {
      data () {
        return {
          timeline: true,
          options: {
            enableCompass: true,
            enableZoomControl: true,
            // enableZoomControl: {
            //   // 缩放比例
            //    zoomAmount: 2,
            //   // 用于在使用重置导航重置地图视图时设置默认视图控制。接受的值是经纬度{lng: number, lat: number, height: number}或者 rectangle{west: number,south: number,east: number,north: number}
            //   defaultResetView: {
            //     lng: 105, lat: 29.999999999999993, height: 19059568.497290563, heading: 360, pitch: -90, roll: 0
            //   },
            //   overrideCamera: false
            // },
            enableDistanceLegend: true,
            enableLocationBar: true,
            // enableLocationBar: {
            //   // 获取更精确的高程
            //   gridFileUrl: './statics/SampleData/WW15MGH.DAC',
            //   rangType: 0 // 0 1 2
            // },
            enableCompassOuterRing: true,
            enablePrintView: true,
            // enablePrintView: {
            //   // 是否添加 Credit
            //   showCredit: true,
            //   // 是否自动打印
            //   printAutomatically: false
            // },
            // enableMyLocation: true,
            enableMyLocation: {
              amap: {
                key: '42d22e6ed83f077bc28b7864718726de'
              }
            }
          }
        }
      },
      methods: {
        ready (cesiumInstance) {
          this.cesiumInstance = cesiumInstance
          const {Cesium, viewer} = this.cesiumInstance
        },
        legendChanged (e) {
          console.log(e)
        }
      }
    }
  </script>